.accordion {
  display: none;
}
@media (max-width: 767px) {
  .accordion {
    display: block;
    margin: 0 auto 35px auto;
  }
}
.accordion .accordion {
  width: 90%;
  margin: 0 auto;
}
.accordion__element:last-child {
  margin-bottom: 0;
}
.accordion__element:nth-child(2n)::before {
  height: 0;
}
.accordion__element {
  width: 100%;
  position: relative;
}
.accordion__element::before {
  display: none;
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 1000%;
  height: 1px;
  background-color: #f2f5f7;
  transform: translateX(-50%);
}
@media (max-width: 767px) {
  .accordion__element::before {
    display: block;
  }
}
.accordion__element::after {
  display: none;
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 1000%;
  height: 1px;
  background-color: #f2f5f7;
  transform: translateX(-50%);
}
@media (max-width: 767px) {
  .accordion__element::after {
    display: block;
  }
}
.accordion__element.is-active {
  padding-bottom: 20px;
}
.accordion__element.is-active .accordion__button::after {
  display: none;
}
@media (max-width: 767px) {
  .accordion__button {
    position: relative;
    width: 100%;
    padding: 20px 0;
    text-transform: uppercase;
    cursor: pointer;
  }
}
.accordion__button::before {
  display: none;
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  width: 15px;
  height: 1px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .accordion__button::before {
    display: block;
  }
}
.accordion__button::after {
  display: none;
  position: absolute;
  content: "";
  top: 50%;
  right: 0;
  width: 15px;
  height: 1px;
  background-color: #ffffff;
  transform: rotate(90deg);
}
@media (max-width: 767px) {
  .accordion__button::after {
    display: block;
  }
}
.accordion__wrapper {
  padding: 10px;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

.phone-mask input[type=tel] {
  font-size: 26px;
  line-height: 30px;
  min-width: 300px;
  border: 2px solid #000000;
  outline: none;
  transition: border 0.3s ease;
}
.phone-mask input[type=tel]:hover, .phone-mask input[type=tel]:focus {
  border-color: #2c39f2;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 68%;
  padding-right: 40px;
  padding-left: 40px;
}
@media (max-width: 1439px) {
  .container {
    max-width: 89%;
    padding-right: 20px;
    padding-left: 20px;
  }
}
@media (max-width: 1023px) {
  .container {
    max-width: 91.5%;
    padding-right: 16px;
    padding-left: 16px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 87.5%;
    padding-right: 5px;
    padding-left: 5px;
  }
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2500;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(3, 16, 28, 0.7);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 319px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  background-color: #00b2ff;
  position: relative;
  width: 500px;
  padding: 40px;
  padding-bottom: 40px;
  border-radius: 3px;
}
@media (max-width: 319px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  z-index: 3000;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 319px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.modal span {
  margin: 0;
  margin-bottom: 15px;
  margin-left: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 141%;
  color: #ffffff;
}

.modal p {
  margin: 0;
  margin-bottom: 40px;
  font-family: "PT Mono", "Arial", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  max-width: 380px;
}

.modal__field-wrap {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.modal__field-wrap input {
  margin-bottom: 20px;
  width: 100%;
  padding: 15px 0 13px 15px;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  font-family: "PT Mono", "Arial", monospace;
}
.modal__field-wrap input:hover {
  opacity: 0.7;
}

.modal__field-wrap input:last-child {
  margin-bottom: 0;
}

.modal__field-wrap input::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  font-family: "PT Mono", "Arial", monospace;
}

.modal form textarea {
  margin-bottom: 19px;
  width: 100%;
  padding: 23px 0 22px 15px;
  background-color: rgba(4, 20, 39, 0.15);
  border: none;
  border-radius: 3px;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  font-family: "PT Mono", "Arial", monospace;
}
.modal form textarea:hover {
  opacity: 0.7;
}

.modal form textarea::placeholder {
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  font-family: "PT Mono", "Arial", monospace;
}

.modal__button {
  width: 100%;
  padding: 13px 60px;
  margin-bottom: 20px;
  flex-shrink: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 0.05em;
  text-align: center;
  text-transform: uppercase;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  background-color: #000000;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
}
.modal__button:hover {
  color: #000000;
  background-color: #ffffff;
}

.modal__button-wrap label {
  position: relative;
  padding-left: 33px;
  font-family: "PT Mono", "Arial", monospace;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  cursor: pointer;
}
.modal__button-wrap label:focus-within {
  outline: 1px solid #026693;
}
.modal__button-wrap label:hover {
  opacity: 0.7;
}

.modal__button-wrap span {
  position: absolute;
  top: 0;
  left: 9px;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.modal__button-wrap input + span::before {
  display: none;
  position: absolute;
  content: "";
  top: 28%;
  left: 2px;
  width: 12px;
  height: 6px;
  background-image: url("../img/svg/check.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.modal__button-wrap input:checked + span::before {
  display: block;
}

.modal__close-btn svg {
  color: #ffffff;
}

.modal__close-btn:hover svg {
  color: #000000;
}

.modal__input-wrap {
  width: 100%;
}

.modal__input-wrap:not(:last-child) {
  margin-bottom: 20px;
}

