@media (max-width: 1023px) {
  .header__span {
    display: none;
  }
}

.header__logo-link {
  display: block;
}
@media (max-width: 767px) {
  .header__logo-link {
    margin-right: 0;
    padding-right: 50px;
  }
}
.header__logo-link:hover {
