  cursor: pointer;
  user-select: none;
}
@media (max-width: 1023px) {
  .contact__button {
    width: 39.5%;
  }
}
@media (max-width: 767px) {
  .contact__button {
    padding: 13px 25px;
    letter-spacing: 0.05em;
    width: 100%;
  }
}
.contact__button:hover {
  color: #000000;
  background-color: #ffffff;
}

.contact__button-wrap label {
  position: relative;
  padding-top: 13px;
  padding-left: 26px;
  font-family: "PT Mono", "Arial", monospace;
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  cursor: pointer;
}
.contact__button-wrap label:focus-within {
  outline: 1px solid #026693;
}
@media (max-width: 1023px) {
  .contact__button-wrap label {
    padding-top: 9px;
  }
}
@media (max-width: 767px) {
  .contact__button-wrap label {
    padding-top: 0;
    padding-left: 30px;
  }
}
.contact__button-wrap label:hover {
  opacity: 0.7;
}

.contact__button-wrap span {
  position: absolute;
  top: 20px;
  left: 0;
  width: 15px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}
@media (max-width: 1023px) {
  .contact__button-wrap span {
    top: 17px;
  }
}
@media (max-width: 767px) {
  .contact__button-wrap span {
    top: 8px;
    left: 5px;
  }
}

.contact__button-wrap input + span::before {
  display: none;
  position: absolute;
  content: "";
  top: 28%;
  left: 2px;
  width: 12px;
  height: 6px;
  background-image: url("../img/svg/check.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.contact__button-wrap input:checked + span::before {
  display: block;
}

.contact__picture {
  z-index: 1000;
  flex-shrink: 1;
  width: 510px;
  height: 285px;
  background-image: url("../img/paper-airplane.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: top 20px right;
}
@media (max-width: 1023px) {
  .contact__picture {
    display: none;
  }
}

.contact__input-wrap {
  width: 100%;
}

.input-message.is-invalid {
  font-size: 14px;
  color: #ffffff;
}

